

@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap');

* {
	box-sizing: border-box;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

body {
	font-family: 'Playfair Display', serif;
	font-weight: normal;
	background: #E4E4E4;
}

.container {
	max-width: 1140px;
	margin: 0 auto;
    @media (max-width: 1200px) {
		max-width: 980px;
		margin: 0 auto;
    }
    @media (max-width: 768px) {
        max-width: 86%;
        margin: 0 auto;
    }
}

.photo {
    transition: opacity 0.4s ease;
    &-head {
        padding: 100px 0 50px;
        position: fixed;
        top: 0;
        left: 50%;
        width: 100%;
        transform: translate(-50%);
        background: #E4E4E4;
        text-align: center;
        z-index: 1000;
    }

    &-head.unfixed {
        position: absolute;
    }

    &-head.unfixed .photo-desc{
        display: block;
    }

    &-head.unfixed .photo-subtitle{
        display: none;
    }

    &-title {
        font-size: 60px;
        line-height: 1.13em;
        margin-bottom: 0.25em;
    }

    &-subtitle {
        font-family: 'Open Sans Condensed', sans-serif;
        font-weight: 300;
        font-size: 24px;
        line-height: 1.37em;
        display: none;
    }

    &-desc {
        font-size: 20px;
        line-height: 1.5em;
        letter-spacing: 0.0185714em;
        max-width: 530px;
        margin: 0 auto;
    }

    &-modal {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0;
        z-index: 2000;
        background-color: rgba($color: #000000, $alpha: 0.3);
        transition: transform 0.4s;
        &__image {
            position: absolute;
            top: 50%;
            left: 50%;
            max-width: 95%;
            max-height: 95%;
            width: auto;
            height: auto;
            transform: translate(-50%, -50%);
            box-shadow: 0 0 20px rgba(0,0,0,0.4);
        }
    }
    &-modal::after {
        content: 'Кликните для закрытия';
        display: block;
        font-size: 12px;
        letter-spacing: 0.08em;
        white-space: nowrap;
        padding: 0.25em;
        border-radius: 0.25em;
        background: #E4E4E4;
        position: absolute;
        bottom: 0.5em;
        left: 50%;
        transform: translateX(-50%);
    }
}



.gallery {
    display: block;
    padding-bottom: 120px;
    position: relative;
    &-inner {
        display: grid;
        grid-gap: 30px;
        grid-template-columns: 1fr 1fr 1fr;
        grid-auto-rows: 60px;
        grid-auto-flow: row dense;
    }

    &-item {
        grid-column-start: auto;
        grid-row-start: auto;
        cursor: pointer;
        &_xs {
            grid-row-end: span 3;
        }
        
        &_s {
            grid-row-end: span 4;
        }
        
        &_m {
            grid-row-end: span 6;
        }
        
        &_l {
            grid-row-end: span 8;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .loading {
        background-image: url('../../loading.svg');
        background-size: contain;
        width: 100px;
        height: 100px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        opacity: 0.5;
    }
}






/* *********** MEDIA ************ */

@media (max-width: 1200px) {
    .photo {
        &-head {
            padding: 70px 0 30px;
        }
    }
    .gallery {
        &-inner {
            grid-gap: 20px;
        }
        &-item {
            grid-column-start: auto;
            grid-row-start: auto;
            cursor: pointer;
            &_xs {
                grid-row-end: span 3;
            }
            
            &_s {
                grid-row-end: span 4;
            }
            
            &_m {
                grid-row-end: span 5;
            }
            
            &_l {
                grid-row-end: span 6;
            }
        }
    }
}

@media (max-width: 768px) {

    .photo {
        &-head {
            padding: 40px 0 20px;
        }
        &-title {
            font-size: 40px;
            margin-bottom: 0;
        }

        &-subtitle {
            display: block;
        }

        &-desc {
            display: none;
            font-size: 20px;
            max-width: 100%;
        }
    }

    .gallery {
        &-inner {
            grid-template-columns: 1fr 1fr;
        }
    }

    

    
}

@media (max-width: 600px) {
    .photo {
        &-head {
            padding: 30px;
        }
        &-title {
            font-size: 40px;
        }

        &-desc {
            font-size: 20px;
            max-width: 100%;
        }
    }

    .gallery {
        &-inner {
            grid-template-columns: 1fr;
        }
    }
}


